body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  margin: 0;
  background-color: gold;
  overflow-x: hidden; }

.app {
  position: relative;
  font-family: 'Inconsolata', monospace; }

.app__header-title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase;
  letter-spacing: 5px; }
  @media all and (min-width: 768px) {
    .app__header-title {
      font-size: 32px; } }

.triangle {
  border-left: 45px solid transparent;
  border-right: 45px solid transparent;
  border-top: 45px solid black;
  position: fixed;
  z-index: -1; }

.t-left {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  left: -30px;
  top: -10px; }

.t-right {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
  right: -30px;
  top: -10px; }

.app__main {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  min-height: 100vh; }

.app__footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-shrink: 0;
          flex-shrink: 0; }

.app__footer-text {
  letter-spacing: 3px;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-size: 18px; }

.circle {
  width: 50px;
  height: 50px;
  background-color: red;
  position: fixed;
  z-index: -1; }

.c-left {
  left: 0;
  bottom: 0;
  border-radius: 0 100% 0 0; }

.c-right {
  right: 0;
  bottom: 0;
  border-radius: 100% 0 0 0; }

.app__field {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

.app__field-search {
  height: 40px;
  width: 300px;
  border: none;
  outline: none;
  text-align: center;
  box-shadow: 0px 0px 10px 0px rgba(197, 197, 197, 0.822); }
  @media all and (min-width: 768px) {
    .app__field-search {
      width: 500px; } }

.pokemons__list {
  margin: 25px 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }
  @media all and (min-width: 768px) {
    .pokemons__list {
      display: grid;
      grid-template-columns: repeat(auto-fill, 300px);
      grid-column-gap: 20px;
      grid-row-gap: 5px;
      -webkit-justify-content: center;
              justify-content: center; } }

.pokemons__list-pokemon {
  list-style: none;
  width: 300px;
  padding: 0;
  margin: 0 0 10px;
  border: 1px solid #cccccc;
  box-shadow: 0px 0px 10px 0px rgba(197, 197, 197, 0.822); }

.pokemon__wrapper {
  position: relative; }

.image__container {
  height: 200px;
  width: 300px;
  background-color: #dbdbdb;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box; }

.pokemon__image {
  height: 100px;
  width: 100px; }

.info__container {
  background-color: white; }

.pokemon__id {
  position: absolute;
  bottom: 143px;
  left: 0px;
  background-color: #cccccc;
  width: 55px;
  height: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #b87511;
  font-size: 14px; }

.pokemon__name {
  text-transform: capitalize;
  margin: 0 0 5px 15px;
  padding-top: 50px; }

.pokemon__types {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  text-align: center;
  font-size: 14px; }

.pokemon__types-item {
  height: 30px;
  width: 70px;
  text-transform: uppercase;
  border: 1px solid #686868;
  border-radius: 15px;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 0 30px 15px; }

